import React, {useContext} from "react";
import {StoreContext} from "./_Store";
import {Link} from "react-router-dom";

export default function ServersStatus(){
        const store = useContext(StoreContext);
        return (
        <div>
        
        <h2>Transport Tycoon Servers List</h2>
        <table id="serversList">
           <tbody>
            {store.state.servers.map((server,index)=>{
                
                if (server.error) {
                    return (
                        <tr key={index}><td>{server.ip}</td><td>{server.name}</td><td>-/-</td><td style={{color: "red"}}>Offline</td><td></td><td></td></tr>
                    );
    
                } else if (server.isLoaded === false) {
    
                    return (
                        <tr key={index}><td>{server.ip}</td><td>{server.name}</td><td>-/-</td><td style={{color: "grey"}}>Loading</td><td></td><td></td></tr>
                    );
                
                } else if (server.playersData === undefined) {
                    return (
                        <tr key={index}><td>{server.ip}</td><td>{server.name}</td><td>-/-</td><td style={{color: "red"}}>ERROR</td><td></td><td></td></tr>
                    );
    
                } else {
                    return (
                        <tr key={index}><td>{server.ip}</td><td>{server.name}</td><td>{server.playersData.length}/{server.serverData.limit}</td><td style={{color: "blue"}}>Online</td><td>{server.lastUpdate}</td><td><Link to={"/?playerslist="+(+index+1)} className="btn btn-primary">Players</Link></td></tr>
                    );
                }
          })}
          </tbody>
        </table>
        </div>
        )


    
}