import React from "react";
import {
  BrowserRouter as Router,
  Route,
  Link,
  Redirect
} from "react-router-dom";
import ServersStatus from "./ServersStatus";
import PlayersList from "./PlayersList";
import PlayersFinder from "./PlayersFinder";
import {StoreProvider} from './_Store';
import "./App.css";

export default function App() {
  return (
    <Router basename="ttstats">
      <StoreProvider>
      
      <header>
        <ul>
          <li><Link to="/">Servers Status</Link></li>
          <li><Link to="?playersfinder">Players Finder</Link></li>
          <li><Link to="?player">player</Link></li>
        </ul>
      </header>

      <main>
        {/* {initAllServers()(StoreProvider)} */}
        <Route component={
          (props)=>{
            const url = props.location.search.split("=");
            switch(url[0]){
              case ("?status"):
                // return <div>test</div>
                return <ServersStatus/>;
              case ("?playerslist"):
                  return <PlayersList url={parseInt(url[1])}/>
              case ("?player"):
                return <div>player</div>;
              case ("?playersfinder"):
                return <PlayersFinder/>;
              default:
                return <Redirect to="/?status"/>;
            }
          }}/>
      </main>
      <footer>
        <p>Footer</p>
      </footer>
      </StoreProvider>
    </Router>
  );
}
